body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 22.5vmin;
  animation: 3600s steps(60, end) infinite hand-move;
  position: absolute;
  bottom: 50%;
  left: 38.5vmin;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 34.5vmin;
  animation: 60s linear infinite hand-move;
  position: absolute;
  bottom: 50%;
  left: 39.25vmin;
}

@keyframes hand-move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}
